<template>
  <div class="regest">
    注册
    <form action="http://localhost:8080/user/register" method="post" id="form">
      <label for="userName">用户名</label>
      <input type="text" name="" id="userName" v-model="userName">
      <label for="userPassword">密码</label>
      <input type="password" name="" id="userPassword" v-model="userPassword">
      <label for="userPassword">确认密码</label>
      <input type="password" id="surePassword" v-model="surePassword">
      <span id="errWarn" v-show="iserr" ></span>
      <label for="userEamil">邮箱</label>
      <input type="text" name="" id="userEamil" v-model="userEamil">
      <label for="userPhone">手机号</label>
      <input type="text" name="" id="userPhone" v-model="userPhone">
      <label for="userQuestion">密保问题</label>
      <select name="userQuestion" id="userQuestion" v-model="userQuestion">
        <option value="0">我的出生地</option>
        <option value="1">我的出生年月</option>
        <option value="2">我的母校名称</option>
        <option value="3">我的班主任名称</option>
      </select>
      <label for="userAnswer">密保答案</label>
      <input type="text" name="userAnswer" id="userAnswer" v-model="userAnswer">
      <input type="submit" value="提交">
    </form>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'HelloWorld',
  data() {
    return {
      userName:'',
      userPassword:'',
      surePassword:'',
      userEamil:'',
      userPhone:'',
      userQuestion:'',
      userAnswer:'',
    }
  },
  methods: {
    
  },
  computed: {
    iserr(){
      if (this.userPassword===this.surePassword){
        return false
      }else{
        return true
      }
    },
    submitobj(){
      return{
        userName:this.userName,
        userPassword:this.userPassword,
        userEamil:this.userEamil,
        userPhone:this.userPhone,
        userQuestion:this.userQuestion,
        userAnswer:this.userAnswer
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
label{
  display: block;
}
#errWarn{
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: red;
  border-radius: 50%;
  box-shadow: 1px 1px 2px red;
  position: relative;
  top: -2px;
}
</style>



