import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import regest from '@/components/regest'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/regest',
      name: 'regest',
      component: regest
    }
  ]
})



